%banner-default {
  background: url('//x.autoimg.cn/cfw/yc/cus/products/src/images/detail-banner.png') center center no-repeat;
  background-size: cover;
}

@keyframes flipInY {
  0% {
    transform: perspective(400px) rotateY(90deg);
    animation-timing-function: ease-in;
    opacity: 0
  }

  40% {
    transform: perspective(400px) rotateY(-20deg);
    animation-timing-function: ease-in
  }

  60% {
    transform: perspective(400px) rotateY(10deg);
    opacity: 1
  }

  80% {
      transform: perspective(400px) rotateY(-5deg);
  }

  to {
    transform: perspective(400px);
  }
}

.flipInY {
  -webkit-backface-visibility: visible!important;
  backface-visibility: visible!important;
  animation-name: flipInY;
  animation-duration: 1.4s;
  animation-fill-mode: both;
}

.tag {
  display: inline-block;
  border-top-left-radius: 4px;
  border-bottom-right-radius: 4px;
  text-align: center;
  font-size: 12px;
  color: #fff;
  font-family: PingFangSC-Semibold;
  font-style: normal;
  margin-right: pr2(4);
  padding: pr2(3) pr2(4);
  box-sizing: border-box;
  line-height: 1;
  &.tag_4s {
    transform: translateY(2px);
    @extend %tag-4s;
  }
  &.tag_service {
    transform: translateY(-2px);
    background: #FF6600;
  }
  &.tag_tou {
    transform: translateY(-1px);
    background: #0055FF;
  }
}

.icon_arrow_r {
  @extend %icon_arrow_r;
}

.business_detail {
  position: relative;
  min-height: 100vh;
  box-sizing: border-box;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
}

.business_header {
  margin: 0 pr2(15);
  margin-top: pr2(12);
  margin-bottom: pr2(12);
  border-radius: 6px;
  overflow: hidden;
  @extend %shadow;
}

.banner_item {
  position: relative;
  z-index: 2;
  display: flex;
  overflow: hidden;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
  .vr_mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    .img {
      width: pr(142);
      height: pr(142);
    }
  }
  .img_cell {
    flex-grow: 1;
    position: relative;
    overflow: hidden;
    @extend %banner-default;
    &:before {
      content: "";
      display: block;
      height: 0;
      padding-top: (167 / 345) * 100%;
      overflow: hidden;
    }
  }
  .video_img {
    position: absolute;
    bottom: pr(26);
    right: pr(40);
    z-index: 100;
    span {
      display: inline-block;
      width: pr(108);
      height: pr(54);
      text-align: center;
      line-height: pr(54);
      color: #fff;
      font-size: pr(24);
      font-family: PingFangSC-Semibold;
      margin-left: pr(24);
      background: rgba(0, 0, 0, 0.5);
      border-radius: 4px;
    }
  }
}

