    &.one_img_video_wrap div {
      width: 100%;
      position: relative;
      overflow: hidden;
      border-radius: pr2(4);
      >img {
        min-width: 100%;
        min-height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &:after {
        content: '';
        display: block;
        height: 0;
        padding-top: (188 / 335) * 100%;
        overflow: hidden;
      }
      img {
        border-radius: pr2(4);
      }
      &:after {
        content: '';
        display: block;
        height: 0;
        padding-top: (188 / 335) * 100%;
        overflow: hidden;
      }
    }
    &.two_img_video_wrap {
      div {
        width: 49.55%;
        position: relative;
        >img {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }
        &:after {
          content: '';
          display: block;
          height: 0;
          padding-top: (124 / 166) * 100%;
          overflow: hidden;
        }
      }
      div:first-of-type {
        img {
          border-radius: pr2(4) 0 0 pr2(4);
        }
      }
      div:last-of-type {
        img {
          border-radius: 0 pr2(4) pr2(4) 0;
        }
      }
    }
    &.three_img_video_wrap {
      div {
        width: 32.84%;
        position: relative;
        >img {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }
        &:after {
          content: '';
          display: block;
          height: 0;
          padding-top: (82 / 110) * 100%;
          overflow: hidden;
        }
      }
      div:first-of-type {
        img {
          border-radius: pr2(4) 0 0 pr2(4);
        }
      }
      div:nth-of-type(3) {
        img {
          border-radius: 0 pr2(4) pr2(4) 0;
        }
      }
    }
    .video_wrap {
      .play_icon_wrap {
        position: absolute;
        top: 50%;
        left: 50%;
        width: pr2(18);
        height: pr2(18);
        transform: translate3d(-50%, -50%, 0);
        z-index: 10;
        background: url('data:image/png;base64,') no-repeat;
        background-size: cover;
        &:after {
          content: '';
          display: block;
          height: 0;
          padding-top: (37 / 40) * 100%;
          overflow: hidden;
        }
      }
    }
    &.one_img_video_wrap {
      .video_wrap {
        .play_icon_wrap {
          width: pr(68);
          height: pr(76);
        }
      }
    }
  }
}

